<template>
  <div>
    <v-navigation-drawer
      temporary
      :right="right"
      v-model="rightDrawer"
      fixed
      app
      color="primary"
      dark
    >
      <v-list>
        <router-link tag="v-list-tile" v-for="item in menu" :key="item.title" :to="item.route">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>{{ item.title }}</v-list-tile-title>
        </router-link>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      app
      :clipped-left="clipped"
      dark
      color="primary"
      flat>
      <v-toolbar-title class="display-2"><h1>Stock Tech</h1></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <router-link tag="v-btn" class="btn btn--flat" v-for="item in menu" :key="item.title" :to="item.route">
          {{ item.title }}
        </router-link>
         <!--  <v-btn flat>Home</v-btn>
        <v-btn flat>About Us</v-btn>
        <v-btn flat>Course</v-btn>
        <v-btn flat>Testimonials</v-btn>
        <v-btn flat>Contact Us</v-btn> -->
      </v-toolbar-items>
      <v-btn icon @click.stop="rightDrawer = !rightDrawer" class="hidden-md-and-up">
        <v-icon>menu</v-icon>
      </v-btn>
    </v-toolbar>
  </div>
</template>
<script>
import { VDivider } from 'vuetify'
export default {
  data () {
    return {
      clipped: false,
      drawer: true,
      fixed: false,
      items: [{
        icon: 'bubble_chart',
        title: 'Inspire'
      }],
      miniVariant: false,
      right: true,
      rightDrawer: false,
      title: 'Stock Tech',
      menu: [
        {
          title: 'Home',
          route: { name: 'home' },
          icon: 'dashboard'
        },
        {
          title: 'About US',
          route: { name: 'about_us' },
          icon: 'question_answer'
        },
        {
          title: 'Our Course',
          route: { name: 'courses' },
          icon: 'security'
        },
        // {
        //   title: 'Testimonials',
        //   route: { name: 'testimonials' },
        //   icon: 'message'
        // },
        {
          title: 'Contact US',
          route: { name: 'contact_us' },
          icon: 'contacts'
        }
      ]
    }
  },
  components: {
    VDivider
  },
  mounted () {
    // this.$vuetify.application.top = 0
    // this.$vuetify.application.footer = 0
  }
}
</script>



